import { tooltipContainer } from "../inversify.config";
import { FindCardBy, getLogger, } from "yugioh-deck-tool-core/src/main";
import { TOOLTIP_TYPES } from "../types";
import { createCardTooltip, createErrorTooltip, createLoadingTooltip, } from "./createCardTooltip";
import { bindReferenceLink } from "./bindReferenceLink";
import { debounce } from "lodash";
import { createPopper } from "@popperjs/core";
const cardDatabase = tooltipContainer.get(TOOLTIP_TYPES.CardDatabase);
const cardDataLoaderService = tooltipContainer.get(TOOLTIP_TYPES.CardDataLoaderService);
const logger = getLogger("bindTooltip");
class CardTooltip {
    constructor(tooltipContainerElement) {
        this.tooltipContainerElement = tooltipContainerElement;
        this.tooltipElement = null;
        this.popperInstance = null;
    }
    close() {
        if (this.tooltipElement != null) {
            this.tooltipElement.remove();
            this.popperInstance?.destroy();
        }
    }
    open(target, tooltip) {
        this.close();
        this.tooltipElement = tooltip;
        this.popperInstance = createPopper(target, this.tooltipElement, {
            placement: "auto",
        });
        this.tooltipContainerElement.appendChild(this.tooltipElement);
    }
}
const loadCard = async (cardKey) => {
    let resolvedCardKey = await cardDatabase.prepareCard(cardKey, FindCardBy.NAME);
    if (resolvedCardKey != null &&
        cardDatabase.hasCard(resolvedCardKey, FindCardBy.NAME)) {
        return cardDatabase.getCard(resolvedCardKey, FindCardBy.NAME);
    }
    resolvedCardKey = await cardDatabase.prepareCard(cardKey, FindCardBy.PASSCODE);
    if (resolvedCardKey != null &&
        cardDatabase.hasCard(resolvedCardKey, FindCardBy.PASSCODE)) {
        return cardDatabase.getCard(resolvedCardKey, FindCardBy.PASSCODE);
    }
    throw new Error(`Could not find card '${cardKey}'.`);
};
export const bindTooltipHandlers = (context, tooltipContainerElement) => {
    const tooltip = new CardTooltip(tooltipContainerElement);
    const openTooltip = (target, cardKey) => {
        logger.trace(`Attempting to show tooltip for '${cardKey}'.`);
        tooltip.open(target, createLoadingTooltip());
        loadCard(cardKey)
            .then((card) => {
            logger.trace("Loaded card.", card);
            tooltip.open(target, createCardTooltip(card));
            if (target instanceof HTMLAnchorElement) {
                bindReferenceLink(target, card);
            }
            // Start request, but do not wait for it to finish.
            cardDataLoaderService
                .updateViews(card)
                .then(() => logger.trace("Updated view count."))
                .catch((e) => logger.warn("Could not update view count.", e));
        })
            .catch((e) => {
            tooltip.open(target, createErrorTooltip("Error while loading card."));
            logger.error("Error while loading card.", e);
        });
    };
    const mouseOverHandler = (event) => {
        const target = event.target;
        if (target instanceof HTMLElement) {
            const cardKey = target.dataset["name"];
            if (cardKey != null) {
                openTooltip(target, cardKey);
            }
        }
    };
    const mouseOutHandler = () => tooltip.close();
    context.addEventListener("mouseover", debounce(mouseOverHandler, 200));
    context.addEventListener("mouseout", mouseOutHandler);
};
